import React, { useContext, useEffect, useRef, useState } from 'react';
import styles from './Console.module.css';
import { BasicContext } from '../contexts/BasicContext';
import { Input } from 'antd';
import { CodeOutlined, HourglassOutlined } from '@ant-design/icons';

export interface ConsoleProps {}

interface PendingInput {
    message?: string;
    resolver: (result: string) => void;
}

export const Console: React.FC<ConsoleProps> = () => {
    const [lines, setLines] = useState<string[]>([]);
    const [immediate, setImmediate] = useState('');
    const ref = useRef<HTMLDivElement>(null);
    const pendingInput = useRef<PendingInput | null>(null);
    const [isWaitingForInput, setIsWaitingForInput] = useState(false);

    const info = useContext(BasicContext);

    // console data
    useEffect(() => {
        setIsWaitingForInput(false);
        return info?.console.addListener(setLines);
    }, [info]);

    // input
    useEffect(() => {
        return info?.input.addListener((resolver) => {
            if (pendingInput.current) {
                pendingInput.current.resolver('');
            }

            pendingInput.current = { resolver };
            setIsWaitingForInput(true);
        });
    }, [info]);

    useEffect(() => {
        // scroll to the bottom
        const scroller = ref.current;

        if (scroller) {
            scroller.scrollTop = scroller.scrollHeight;
        }
    }, [lines]);

    return (
        <div className={styles.console}>
            <div className={styles.output} ref={ref}>
                {lines.map((line, index) => (
                    <div key={`line-${index}`}>{line}</div>
                ))}
            </div>
            <Input
                addonBefore={
                    isWaitingForInput ? <HourglassOutlined /> : <CodeOutlined />
                }
                placeholder={
                    isWaitingForInput ? 'Waiting for input' : undefined
                }
                value={immediate}
                onInput={(ev) => setImmediate(ev.currentTarget.value)}
                onPressEnter={async (element) => {
                    if (isWaitingForInput) {
                        pendingInput.current?.resolver(
                            element.currentTarget.value
                        );
                        pendingInput.current = null;
                        setIsWaitingForInput(false);
                        setImmediate('');
                    } else {
                        await info?.runStatement(element.currentTarget.value);
                        setImmediate('');
                    }
                }}
            />
        </div>
    );
};
