import { useContext, useEffect } from 'react';
import { BasicContext } from '../contexts/BasicContext';
import { notification } from 'antd';

const UnsupportedFeatures = () => {
    const info = useContext(BasicContext);

    useEffect(() => {
        if (info) {
            return info.list.addListener(() => {
                // show an alert
                notification.error({
                    message: 'LIST is not supported!',
                    description:
                        'Just use the text editor below you big buffoon.',
                });
            });
        }
    }, [info]);

    useEffect(() => {
        if (info) {
            return info.save.addListener(() => {
                // show an alert
                notification.error({
                    message: 'SAVE is not supported yet!',
                    description:
                        'For now, be happy being able to copy and paste code like a barbarian.',
                });
            });
        }
    }, [info]);

    useEffect(() => {
        if (info) {
            return info.load.addListener(() => {
                // show an alert
                notification.error({
                    message: 'LOAD is not supported!',
                    description:
                        'For now, be happy being able to copy and paste code like a barbarian.',
                });
            });
        }
    }, [info]);

    return null;
};

export default UnsupportedFeatures;
