import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';

interface Range {
    start: number;
    end: number;
    type: string[];
}

/**
 * Create a new set of non overlapping ranges based on incoming possibly overlapping ranges.
 *
 * Scenarios: (x is new)
 *
 * A:   ==========
 * B: -----
 * C: --xxx=======
 *
 * A: ==========
 * B:       -------
 * C: ======xxxx---
 *
 * A: ==========
 * B:   ----
 * C: ==xxxx====
 *
 * A: =======
 * B:   -----
 * C: ==xxxxx
 *
 * A: =======
 * B: --
 * C: xx=====
 */

export function mergeRanges(ranges: Range[]): Range[] {
    let results: Range[] = [];

    // sort the ranges from start to end
    const inactiveRanges = Array.from(ranges).sort((a, b) =>
        a.start < b.start ? -1 : 1
    );

    if (inactiveRanges.length > 1) {
        const min = inactiveRanges[0].start;
        const max = Math.max(...inactiveRanges.map((r) => r.end));
        const activeRanges: Range[] = [];
        let lastTypes: string[] = [];
        let lastStart = min;

        for (let i = min; i <= max; i++) {
            // check that we are out of any ranges
            for (let j = 0; j < activeRanges.length; j++) {
                const r = activeRanges[j];

                if (r.end <= i) {
                    activeRanges.splice(j, 1);
                    j--;
                }
            }

            // check for any new ranges
            for (let j = 0; j < inactiveRanges.length; j++) {
                const r = inactiveRanges[j];
                if (r.start === i) {
                    activeRanges.push(r);
                    inactiveRanges.splice(j, 1);
                    j--;
                }
            }

            const activeTypes = activeRanges.reduce(
                (acc, r) => [...acc, ...r.type],
                [] as string[]
            );
            if (
                activeTypes.length !== lastTypes.length ||
                !activeTypes.every((v, i) => v === lastTypes[i])
            ) {
                if (i !== 0) {
                    results.push({
                        start: lastStart,
                        end: i,
                        type: lastTypes,
                    });
                }

                lastTypes = activeTypes;
                lastStart = i;
            }
        }

        if (activeRanges.length) {
            const activeTypes = activeRanges.reduce(
                (acc, r) => [...acc, ...r.type],
                [] as string[]
            );
            results.push({
                start: lastStart,
                end: max,
                type: lastTypes,
            });
        }
    }

    return results;
}
