import React from 'react';
import useWorker from '../hooks/useWorker';
import { BasicContextInterface } from '../interfaces';

export const BasicContext = React.createContext<BasicContextInterface | null>(
    null
);

export const BasicProvider: React.FC = ({ children }) => {
    const context = useWorker();

    return (
        <BasicContext.Provider value={context}>
            {children}
        </BasicContext.Provider>
    );
};
