export const helpFiles: { [key: string]: string } = {};

interface HelpEntry<T = string> {
    title: T;
    key: number | string;
    children?: HelpEntry<T>[];
}

function entry(title: string, helpFile: string = '') {
    const key = title.toLowerCase();

    if (helpFile) {
        helpFiles[key] = helpFile;
    }

    return {
        key,
        title,
        children: [],
    };
}

