export function walkHelp(visitor: (node: HelpEntry) => void) {
    function visit(root: HelpEntry) {
        visitor(root);

        if (root.children && root.children.length) {
            root.children.forEach(visit);
        }
    }

    helpData.forEach(visit);
}

export function filterHelp(
    include: (node: HelpEntry) => boolean,
    transform?: (node: HelpEntry) => HelpEntry<React.ReactNode>
) {
    function visit(result: HelpEntry<React.ReactNode>[], child: HelpEntry) {
        if (include(child)) {
            result.push(transform ? transform(child) : child);
            return result;
        }
        if (Array.isArray(child.children)) {
            const children = child.children.reduce(visit, []);
            if (children.length) result.push({ ...child, children });
        }
        return result;
    }

    return helpData.reduce(visit, []);
}
