import React, { useMemo, useState } from 'react';
import styles from './App.module.css';
import { Collapse, Layout, Space, Checkbox } from 'antd';
import Editor, { EditorSpan, Gutter } from './components/Editor';
import Lexer from './language/lexer';
import { TokenCategory, TokenType } from './language/tokens';
import solarizedLight from './themes/solarized-light';
import { Console } from './components/Console';
import { BasicProvider } from './contexts/BasicContext';
import { Parser } from './language/parser';
import { PlayButton } from './components/PlayButton';
import { StopButton } from './components/StopButton';
import { mergeRanges } from './helpers/ranges';
import { CloseCircleOutlined } from '@ant-design/icons';
import { renumberCode } from './helpers/code';
import { Cursor } from './interfaces';
import { ImagePreview } from './components/ImagePreview';
import Help from './components/Help';
import UnsupportedFeatures from './components/UnsupportedFeatures';

const { Header, Content } = Layout;

const editorStyles = solarizedLight;

function categoryToStyle(category: TokenCategory): string | null {
    switch (category) {
        case TokenCategory.NUMBER:
            return 'number';
        case TokenCategory.STRING:
            return 'string';
        case TokenCategory.COMMENT:
            return 'comment';
        case TokenCategory.STATEMENT:
            return 'statement';
        default:
            return null;
    }
}

function parseLine(line: string) {
    const l = new Lexer(line);

    const spans: EditorSpan[] = [];

    const p = new Parser(new Lexer(line));
    const statement = p.parseStatement();
    let gutter: Gutter = {};

    if (p.errors.length) {
        spans.push({ start: 0, end: line.length, type: ['error'] });
        gutter.icon = <CloseCircleOutlined style={{ color: 'red' }} />;
        gutter.helpText = p.errors[0];
    }

    let t = l.nextToken();
    while (t.type !== TokenType.EOF) {
        const styles = [];

        const catStyle = categoryToStyle(t.category);
        if (catStyle) {
            styles.push(catStyle);
        }

        if (spans.length === 0 && t.type === TokenType.INT) {
            styles.push('lineNumber');
        }

        if (styles.length) {
            spans.push({
                type: styles,
                start: t.start,
                end: t.end,
            });
        }

        t = l.nextToken();
    }

    if (statement) {
        const refs = statement.getLineNumberReferences();
        refs.forEach((ref) => {
            if (ref.lineNumberToken) {
                spans.push({
                    start: ref.lineNumberToken.start,
                    end: ref.lineNumberToken.end,
                    type: ['lineNumberRef'],
                });
            }
        });
    }

    return { spans: mergeRanges(spans) as EditorSpan[], gutter };
}

function App() {
    const [code, setCode] = useState(`10 FOR Y = 0 TO 8
20 GOSUB 50
30 NEXT
40 END
50 PRINT "hey"
60 RETURN`);
    const [helpLookup, setHelpLookup] = useState('');
    const [autoFormat, setAutoFormat] = useState(true);

    const lookupHelp = (start: Cursor, end: Cursor) => {
        if (start.line === end.line) {
            const line = code.split('\n')[start.line - 1];

            if (line) {
                const l = new Lexer(line);
                let tok = l.nextToken();
                do {
                    if (
                        tok.start <= start.column - 1 &&
                        tok.end >= end.column - 1
                    ) {
                        if (
                            tok.category === TokenCategory.STATEMENT ||
                            tok.category === TokenCategory.IDENT
                        ) {
                            setHelpLookup(tok.literal);
                            return;
                        }
                    }

                    tok = l.nextToken();
                } while (tok.type !== TokenType.EOF);
            }
        }

        setHelpLookup('');
    };

    const editorSpans = useMemo<{
        spans: EditorSpan[][];
        gutters: Gutter[];
    }>(() => {
        let lineSpans: EditorSpan[][] = [];
        let gutters: Gutter[] = [];

        code.split('\n').forEach((line) => {
            const { spans, gutter } = parseLine(line);

            lineSpans.push(spans);
            gutters.push(gutter);
        });

        return { spans: lineSpans, gutters };
    }, [code]);

    return (
        <div className={styles.app}>
            <BasicProvider>
                <UnsupportedFeatures />
                <Layout className={styles.layout}>
                    <Header />
                    <Content className={styles.appContainer}>
                        <div className={styles.content}>
                            <div className={styles.top}>
                                <div className={styles.console}>
                                    <Console />
                                </div>
                            </div>
                            <div className={styles.toolbar}>
                                <Space>
                                    <PlayButton code={code} />
                                    <StopButton />
                                </Space>
                                <div style={{ flexGrow: 1 }} />
                                <Space>
                                    <Checkbox
                                        checked={autoFormat}
                                        onChange={(value) =>
                                            setAutoFormat(value.target.checked)
                                        }
                                    >
                                        Auto-format
                                    </Checkbox>
                                    </Space>
                            </div>
                            <Editor
                                className={styles.editor}
                                code={code}
                                onChange={(code) => setCode(code)}
                                styles={editorStyles}
                                spans={editorSpans.spans}
                                gutters={editorSpans.gutters}
                                onCursorChange={(start, end) =>
                                    lookupHelp(start, end)
                                }
                                reformat={(code) =>
                                    autoFormat
                                        ? renumberCode(code, undefined)
                                        : code
                                }
                            />
                        </div>
                        <div className={styles.sidePane}>
                            <Collapse
                                accordion
                                defaultActiveKey="help"
                                className={styles.accordion}
                            >
                                <Collapse.Panel
                                    header="Image Preview"
                                    key="imagePreview"
                                >
                                    <div className={styles.image}>
                                        <ImagePreview />
                                    </div>
                                </Collapse.Panel>
                                <Collapse.Panel key="help" header="Help">
                                    <Help lookup={helpLookup} />
                                </Collapse.Panel>
                            </Collapse>
                        </div>
                    </Content>
                </Layout>
            </BasicProvider>
        </div>
    );
}

export default App;
