import { useEffect, useState } from 'react';
import {
    getIndexFromLineAndColumn,
    getLineAndColumnFromIndex,
} from '../helpers/code';

export function useCursorPosition(ref: HTMLTextAreaElement | null) {
    const [cursorPosition, setCursorPosition] = useState<[number, number]>([
        0, 0,
    ]);

    useEffect(() => {
        function listener() {
            const selectionStart = ref?.selectionStart || 0;
            const selectionEnd = ref?.selectionEnd || 0;

            if (
                selectionStart !== cursorPosition[0] ||
                selectionEnd !== cursorPosition[1]
            ) {
                setCursorPosition([selectionStart, selectionEnd]);
            }
        }

        if (ref) {
            ref.addEventListener('keydown', listener);
            ref.addEventListener('keyup', listener);
            ref.addEventListener('mousedown', listener);
            ref.addEventListener('mouseup', listener);

            return () => {
                ref.removeEventListener('keydown', listener);
                ref.removeEventListener('mousedown', listener);
                ref.removeEventListener('keyup', listener);
                ref.removeEventListener('mouseup', listener);
            };
        }
    }, [ref, cursorPosition]);

    return cursorPosition;
}

export function notifyTextAreaChange(textArea: HTMLTextAreaElement) {
    const changeEvent = new Event('input', {
        bubbles: true,
        cancelable: false,
    });
    textArea.dispatchEvent(changeEvent);
}

export function reformatTextArea(
    textArea: HTMLTextAreaElement | null,
    formatter: (input: string) => string,
    onlyCallOnChangeOnChange = false,
) {
    if (textArea) {
        const currentCode = textArea.value;
        const [originalStartLine, originalStartColumn] =
            getLineAndColumnFromIndex(currentCode, textArea.selectionStart);
        const [originalEndLine, originalEndColumn] = getLineAndColumnFromIndex(
            currentCode,
            textArea.selectionEnd
        );

        const result = formatter(currentCode).replace(/^(\d+)$/gm, (match) => {
            return match + ' ';
        })

        if (!onlyCallOnChangeOnChange || result !== currentCode) {
            textArea.selectionStart = 0;
            textArea.selectionEnd = textArea.value.length;
            textArea.setRangeText(result);

            textArea.selectionStart = getIndexFromLineAndColumn(
                result,
                originalStartLine,
                originalStartColumn
            );

            textArea.selectionEnd = getIndexFromLineAndColumn(
                result,
                originalEndLine,
                originalEndColumn
            );

            notifyTextAreaChange(textArea);

            return true;
        }
    }

    return false;
}
