import { Button } from 'antd';
import React, { useContext } from 'react';
import { BasicContext } from '../contexts/BasicContext';
import { CaretRightFilled } from '@ant-design/icons';
import { useBasicContextState } from '../hooks/BasicContextState';
import { ContextState } from '../language/context';

export interface PlayButtonProps {
    code: string;
}

export const PlayButton: React.FC<PlayButtonProps> = ({ code }) => {
    const info = useContext(BasicContext);
    const contextState = useBasicContextState();

    return (
        <Button
            type="primary"
            icon={<CaretRightFilled />}
            onClick={() => {
                info?.runProgram(code);
            }}
            disabled={contextState !== ContextState.IDLE}
        />
    );
};
