import { Button } from 'antd';
import React, { useContext } from 'react';
import { BasicContext } from '../contexts/BasicContext';
import { CloseCircleFilled } from '@ant-design/icons';
import { useBasicContextState } from '../hooks/BasicContextState';
import { ContextState } from '../language/context';

export const StopButton: React.FC = () => {
    const info = useContext(BasicContext);
    const contextState = useBasicContextState();

    return (
        <Button
            type="primary"
            icon={<CloseCircleFilled />}
            onClick={() => {
                info?.stop();
            }}
            disabled={contextState !== ContextState.RUNNING}
        />
    );
};
