import { useContext, useEffect, useState } from 'react';
import { BasicContext } from '../contexts/BasicContext';
import { ContextState } from '../language/context';

export function useBasicContextState(): ContextState {
    const info = useContext(BasicContext);
    const [state, setState] = useState(ContextState.IDLE);

    useEffect(() => {
        if (info) {
            return info.state.addListener(setState);
        }
    }, [info]);

    return state;
}
