import { Tree, Input, Card, Space, Button } from 'antd';
import { filterHelp, helpFiles, walkHelp } from '../help';
import styles from '../App.module.css';
import ReactMarkdown from 'react-markdown';
import React, { ChangeEvent, useEffect, useState } from 'react';
import { UnlockOutlined, LockOutlined } from '@ant-design/icons';

export interface HelpProps {
    lookup?: string;
}

const Help: React.FC<HelpProps> = ({ lookup }) => {
    const [helpFile, setHelpFile] = useState<string | null>(null);
    const [helpContent, setHelpContent] = useState('');
    const [expandedKeys, setExpandedKeys] = useState<(string | number)[]>([]);
    const [searchValue, setSearchValue] = useState('');
    const [autoExpandParent, setAutoExpandParent] = useState(true);
    const [locked, setLocked] = useState(false);
    const [selected, setSelected] = useState<string | number | undefined>();

    useEffect(() => {
        if (lookup) {
            if (!locked) {
                const keys: (string | number)[] = [];
                walkHelp((e) => {
                    if (
                        e.title.toLowerCase().indexOf(lookup.toLowerCase()) >= 0
                    ) {
                        keys.push(e.key);
                    }
                });

                if (keys.length === 1) {
                    setSelected(keys[0]);
                }

                setExpandedKeys(keys);
                setSearchValue(lookup);
                setAutoExpandParent(true);
            }
        }
    }, [lookup, locked]);

    useEffect(() => {
        async function loadHelp(file: string) {
            const response = await fetch(file);
            const text = await response.text();

            setHelpContent(text);
        }

        setHelpContent('');

        if (helpFile) {
            loadHelp(helpFile);
        }
    }, [helpFile]);

    useEffect(() => {
        if (selected && helpFiles[selected]) {
            setHelpFile(helpFiles[selected]);
        }
    }, [selected]);

    const onExpand = (expandedKeys: (string | number)[]) => {
        setExpandedKeys(expandedKeys);
        setAutoExpandParent(false);
    };

    const onSearchChange = (ev: ChangeEvent<HTMLInputElement>) => {
        const { value } = ev.target;

        const keys: (string | number)[] = [];
        walkHelp((e) => {
            if (e.title.toLowerCase().indexOf(value.toLowerCase()) >= -1) {
                keys.push(e.key);
            }
        });

        setExpandedKeys(keys);
        setSearchValue(value);
        setAutoExpandParent(true);
    };

    const filteredHelp = filterHelp(
        (e) => e.title.toLowerCase().indexOf(searchValue.toLowerCase()) >= 0,
        (e) => {
            const where = e.title
                .toLowerCase()
                .indexOf(searchValue.toLowerCase());
            const before = e.title.substring(0, where);
            const middle = e.title.substring(where, where + searchValue.length);
            const after = e.title.substring(where + searchValue.length);

            return {
                ...e,
                title: (
                    <span>
                        {before}
                        <strong>{middle}</strong>
                        {after}
                    </span>
                ),
            };
        }
    );

    return (
        <>
            <Space direction="vertical" style={{ display: 'flex' }}>
                <Card style={{ maxHeight: '200px', overflow: 'auto' }}>
                    <Space direction="vertical" style={{ display: 'flex' }}>
                        <Input
                            placeholder="Search..."
                            onChange={onSearchChange}
                            value={searchValue}
                        />
                        <Tree
                            style={{
                                width: '100%',
                                overflow: 'auto',
                            }}
                            treeData={filteredHelp}
                            blockNode
                            onSelect={(
                                _,
                                { selectedNodes: [selectedNode] }
                            ) => {
                                setSelected(selectedNode.key);
                            }}
                            selectedKeys={
                                selected ? [] : [selected as string | number]
                            }
                            onExpand={onExpand}
                            expandedKeys={expandedKeys}
                            autoExpandParent={autoExpandParent}
                        />
                    </Space>
                </Card>
                {helpContent && (
                    <Card
                        extra={
                            <Button
                                icon={
                                    locked ? (
                                        <LockOutlined />
                                    ) : (
                                        <UnlockOutlined />
                                    )
                                }
                                onClick={() => setLocked(!locked)}
                            />
                        }
                    >
                        <div className={styles.helpContent}>
                            <ReactMarkdown>{helpContent}</ReactMarkdown>
                        </div>
                    </Card>
                )}
            </Space>
        </>
    );
};

export default Help;
