import Lexer from '../language/lexer';
import { Parser } from '../language/parser';
import { LineNumberReference } from '../language/ast';
import { Cursor } from '../interfaces';

interface RenumberOptions {
    lineStep?: number;
}

export function renumberCode(
    code: string,
    options: RenumberOptions = {}
): string {
    const { lineStep = 10 } = options;

    let currentLine = lineStep;

    const parsedLines = code.split('\n').map((line) => {
        const lexer = new Lexer(line);
        const parser = new Parser(lexer);
        const statement = parser.parseStatement();

        return { line, statement: parser.errors.length > 0 ? null : statement };
    });

    // initialize an array of line number references
    const lineReferences: { [line: number]: LineNumberReference[] } = {};
    parsedLines.forEach(({ statement }) => {
        if (statement) {
            if (statement.lineNumber !== undefined) {
                lineReferences[statement.lineNumber] = [];
            }
        }
    });

    parsedLines.forEach(({ statement }) => {
        if (statement) {
            const refs = statement?.getLineNumberReferences();

            refs?.forEach((ref) => {
                lineReferences[ref.lineNumber]?.push(ref);
            });
        }
    });

    const mutations: [number, number][] = [];

    for (let i = 0; i < parsedLines.length; i++) {
        const { statement } = parsedLines[i];

        if (statement?.lineNumber && statement.lineNumber !== currentLine) {
            // this line number needs to change
            mutations.push([statement.lineNumber, currentLine]);
            statement.lineNumber = currentLine;
        } else if (statement) {
            statement.lineNumber = currentLine;
        }

        currentLine += lineStep;
    }

    mutations.forEach(([fromLine, toLine]) => {
        const refs = lineReferences[fromLine];

        if (refs) {
            refs.forEach((ref) => {
                ref.lineNumberToken.literal = `${toLine}`;
                ref.lineNumber = toLine;
            });
        }
    });

    const newCode = parsedLines
        .map(({ statement, line }) => (statement ? statement.toString() : line))
        .join('\n');

    return newCode;
}

export function getLineAndColumnFromIndex(
    code: string,
    index: number
): [line: number, column: number] {
    let l = 1;
    let c = 1;

    for (let i = 0; i < index && i < code.length; i++) {
        if (code[i] === '\n') {
            l++;
            c = 1;
        } else {
            c++;
        }
    }

    return [l, c];
}

export function getIndexFromLineAndColumn(
    code: string,
    line: number,
    column: number
) {
    const lines = code.split('\n');
    return (
        lines
            .slice(0, line - 1)
            .reduce((acc, line) => acc + line.length + 1, 0) +
        Math.min(lines[line - 1].length, column - 1)
    );
}
