/// <reference path="../worker.d.ts" />
import { useEffect, useState, useCallback } from 'react';
// eslint-disable-next-line import/no-webpack-loader-syntax
import Worker from 'worker-loader!../worker';
import * as ComLink from 'comlink';
import {
    BasicContextInterface,
    ConsoleListener,
    ImageListener,
    InputListener,
    ListListener,
    LoadListener,
    SaveListener,
    StateListener,
} from '../interfaces';

const useWorker = () => {
    const [context, setContext] = useState<BasicContextInterface | null>(null);

    const createWorker = useCallback(() => {
        const worker = Worker();
        const context = ComLink.wrap<any>(
            worker
        ) as unknown as BasicContextInterface;

        const contextProxy = {
            console: {
                addListener: (cb: ConsoleListener) =>
                    context!.console.addListener(ComLink.proxy(cb)),
                print: (m: string) => context!.console.print(m),
                clear: () => context!.console.clear(),
            },
            input: {
                addListener: (cb: InputListener) =>
                    context!.input!.addListener(ComLink.proxy(cb)),
            },
            image: {
                addListener: (cb: ImageListener) =>
                    context!.image.addListener(ComLink.proxy(cb)),
            },
            runProgram: (p: string) => context!.runProgram(p),
            runStatement: (p: string) => context!.runStatement(p),
            stop: () => {
                worker.terminate();
                setContext(createWorker());
            },
            state: {
                addListener: (cb: StateListener) =>
                    context!.state.addListener(ComLink.proxy(cb)),
            },
            list: {
                addListener: (cb: ListListener) =>
                    context!.list.addListener(ComLink.proxy(cb)),
            },
            save: {
                addListener: (cb: SaveListener) =>
                    context!.save.addListener(ComLink.proxy(cb)),
            },
            load: {
                addListener: (cb: LoadListener) =>
                    context!.load.addListener(ComLink.proxy(cb)),
            },
        };

        return contextProxy;
    }, []);

    useEffect(() => {
        setContext(createWorker());
    }, [createWorker]);

    return context;
};

export default useWorker;
