import React, {
    CSSProperties,
    ReactNode,
    useCallback,
    useEffect,
    useMemo,
    useRef,
    useState,
} from 'react';
import styles from './Editor.module.css';
import { classes } from '../helpers';
import { Tooltip } from 'antd';
import {
    notifyTextAreaChange,
    reformatTextArea,
    useCursorPosition,
} from '../hooks/TextArea';
import {
    getIndexFromLineAndColumn,
    getLineAndColumnFromIndex,
} from '../helpers/code';
import { Cursor } from '../interfaces';

export interface Gutter {
    icon?: ReactNode;
    helpText?: string;
}

export interface EditorProps {
    code: string;
    onChange: (code: string) => void;
    className?: string;
    spans?: EditorSpan[][];
    gutters?: Gutter[];
    styles?: {
        [key: string]: CSSProperties;
    };
    onCursorChange?: (cursorStart: Cursor, cursorEnd: Cursor) => void;
    reformat?: (code: string) => string;
}

export interface EditorSpan {
    start: number;
    end: number;
    type: string[];
}

function styledText(
    text: string,
    spans: EditorSpan[][],
    styles: {
        [key: string]: CSSProperties;
    }
) {
    const lines: React.ReactNode[] = [];

    text.split('\n').forEach((line, lineNumber) => {
        let str = line;

        const result = [];
        let lastIndex = 0;

        for (let i = 0; i < spans[lineNumber].length; i++) {
            const span = spans[lineNumber][i];

            const pre = line.substring(lastIndex, span.start);
            if (pre) {
                result.push(pre);
            }

            const sub = str.substring(span.start, span.end);
            result.push(
                React.createElement(
                    'span',
                    {
                        key: `${lineNumber}-${span.start}`,
                        style: span.type.reduce(
                            (prev, key) => ({ ...prev, ...styles[key] }),
                            {}
                        ),
                    },
                    [sub]
                )
            );

            lastIndex = span.end;
        }

        if (lastIndex < line.length) {
            result.push(line.substring(lastIndex));
        }

        if (result.length === 0) {
            result.push(' ');
        }

        lines.push(<div key={`line-${lineNumber}`}>{result}</div>);
    });

    return lines;
}

function textAreaKeyboardEventHandler(
    ev: KeyboardEvent,
    formatter: (code: string) => string
) {
    const textArea = ev.target as HTMLTextAreaElement;

    switch (ev.key.toLowerCase()) {
        case 'arrowup':
        case 'arrowdown':
            // just a cursor move, reformat the text
            reformatTextArea(textArea, formatter);
            break;
        case 'enter':
            // cancel the event
            ev.preventDefault();
            ev.stopPropagation();

            const pos = getLineAndColumnFromIndex(
                textArea.value,
                textArea.selectionStart
            );

            // manually add a newline
            textArea.setRangeText('\n');

            // run the formatter to insert the new numbered line
            reformatTextArea(textArea, formatter);

            const line = textArea.value.split('\n')[pos[0]];

            // set the cursor to the new numbered line
            textArea.selectionStart = textArea.selectionEnd =
                getIndexFromLineAndColumn(
                    textArea.value,
                    pos[0] + 1,
                    line.length + 1
                );
            break;
        case 'backspace':
            // cancel the event
            ev.preventDefault();
            ev.stopPropagation();

            const prevNumberOfLines = textArea.value.split('\n').length;

            // delete the text
            if (textArea.selectionStart !== textArea.selectionEnd) {
                textArea.setRangeText('');
            } else {
                // delete a single character?
                textArea.setRangeText(
                    '',
                    Math.max(0, textArea.selectionStart - 1),
                    textArea.selectionStart
                );
            }

            const newNumberOfLines = textArea.value.split('\n').length;

            if (prevNumberOfLines !== newNumberOfLines) {
                // reformat
                reformatTextArea(textArea, formatter);
            } else {
                // just dispatch the change event since we did change the text area
                notifyTextAreaChange(textArea);
            }
            break;
    }
}

function textAreaBlurEventHandler(
    ev: Event,
    formatter: (code: string) => string
) {
    const textArea = ev.target as HTMLTextAreaElement;
    
    if (reformatTextArea(textArea, formatter, true)) {
        textArea.blur();
    }            
}

export const Editor: React.FC<EditorProps> = ({
    code,
    spans = [],
    styles: textStyles = {},
    onChange,
    className,
    gutters,
    onCursorChange,
    reformat,
}) => {
    const [scroll, setScroll] = useState<[number, number]>([0, 0]);
    const ref = useRef<HTMLPreElement | null>(null);
    const gutterRef = useRef<HTMLDivElement | null>(null);
    const [textArea, setTextArea] = useState<HTMLTextAreaElement | null>(null);
    const textAreaCallback = useCallback((node: HTMLTextAreaElement | null) => {
        setTextArea(node);
    }, []);

    useEffect(() => {
        if (textArea && reformat) {
            const keyboardFn = (ev: KeyboardEvent) =>
                textAreaKeyboardEventHandler(ev, reformat);

            const blurFn = (ev: Event) =>
                textAreaBlurEventHandler(ev, reformat);

            textArea.addEventListener('keydown', keyboardFn);
            textArea.addEventListener('focusout', blurFn);

            return () => {
                textArea.removeEventListener('keydown', keyboardFn);
                textArea.addEventListener('focusout', blurFn);
            };
        }
    }, [textArea, reformat]);

    const contents = useMemo(() => {
        return styledText(code, spans, textStyles);
    }, [code, spans, textStyles]);

    useEffect(() => {
        const [scrollTop, scrollLeft] = scroll;

        if (ref.current) {
            ref.current.scrollTop = scrollTop;
            ref.current.scrollLeft = scrollLeft;
        }
        if (gutterRef.current) {
            gutterRef.current.scrollTop = scrollTop;
        }
    }, [scroll]);

    const onCodeChange = (code: string) => {
        onChange(code);
    };

    const [cursorStart, cursorEnd] = useCursorPosition(textArea);

    const [startLine, startColumn] = useMemo(
        () => getLineAndColumnFromIndex(textArea?.value ?? '', cursorStart),
        [textArea, cursorStart]
    );
    const [endLine, endColumn] = useMemo(
        () =>
            cursorEnd !== cursorStart
                ? getLineAndColumnFromIndex(textArea?.value ?? '', cursorEnd)
                : [startLine, startColumn],
        [textArea, cursorStart, cursorEnd, startLine, startColumn]
    );

    useEffect(() => {
        onCursorChange &&
            onCursorChange(
                { line: startLine, column: startColumn },
                { line: endLine, column: endColumn }
            );
    }, [startLine, startColumn, endLine, endColumn, onCursorChange]);

    const {
        default: { backgroundColor, caretColor, ...preStyles },
        gutter,
        statusBar,
    } = textStyles;

    function renderGutterIcon(gutter: Gutter, line: number) {
        if (!gutter.icon) {
            return <div key={line} className={styles.gutterContainer} />;
        }

        return (
            <div key={line} className={styles.gutterContainer}>
                <Tooltip title={gutter.helpText}>{gutter.icon}</Tooltip>
            </div>
        );
    }

    return (
        <div className={classes(styles.editor, className)}>
            <div className={styles.background} style={{ backgroundColor }} />
            <textarea
                wrap="off"
                autoComplete="off"
                autoCorrect="off"
                spellCheck="false"
                autoCapitalize="off"
                ref={textAreaCallback}
                className={classes(styles.textarea, styles.shared)}
                style={{ caretColor }}
                onChange={(ev) =>
                    onCodeChange((ev.target as HTMLTextAreaElement).value)
                }
                onScroll={(ev) =>
                    setScroll([
                        (ev.target as HTMLTextAreaElement).scrollTop,
                        (ev.target as HTMLTextAreaElement).scrollLeft,
                    ])
                }
                value={code}
            />
            <pre
                ref={ref}
                className={classes(styles.code, styles.shared)}
                style={preStyles}
            >
                {contents}
            </pre>
            {gutters ? (
                <div className={styles.gutter} style={gutter} ref={gutterRef}>
                    {gutters.map(renderGutterIcon)}
                </div>
            ) : null}
            <div className={styles.statusBar} style={statusBar}>
                <div className={styles.position}>
                    Line: {startLine} Col: {startColumn}
                    {(endColumn !== startColumn || endLine !== startLine) && (
                        <>
                            {' '}
                            - Line: {endLine} Col: {endColumn}
                        </>
                    )}
                </div>
            </div>
        </div>
    );
};

export default Editor;
